<template>
	<div class="buy-sell-toggle-block">
		<label class="title" v-if="title">{{ title }}</label>
		<div class="buy-sell-toggle-container">
			<div class="buy-sell-toggle left" :class="value == 0 ? 'selected' : 'default'" @click="toggle(0)">
				<label class="text" :class="value == 0 ? 'selected' : ''">{{ $localizeFilter(variants[0]) }}</label>
			</div>
			<div class="buy-sell-toggle right" :class="value == 1 ? 'selected' : 'default'" @click="toggle(1)">
				<label class="text" :class="value == 1 ? 'selected' : ''">{{ $localizeFilter(variants[1]) }}</label>
			</div>
		</div>
	</div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'

export default defineComponent({
	props: ['title', 'variants', 'selected'],
	watch: {
		selected: function (newVal, oldVal) {

			if (newVal != null && newVal != undefined) {
				this.value = this.variants.indexOf(newVal)
			}
		}
	},
	data: () => ({
		value: 0
	}),
	methods: {
		toggle(newVal) {
			this.value = newVal
			this.$emit('toggle', Number(this.value))
		}
	},

})
</script>

<style scoped>
.buy-sell-toggle-block {
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: flex-start;
}

.buy-sell-toggle-container {
	display: inline-block;
	border: 1px solid #90CAFF;
	width: 330px;
	height: 50px;
	position: relative;
	margin: 0;
}

.buy-sell-toggle {
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: center;
	content: "" !important;
	width: calc(50% + 2px);
	height: calc(100% + 2px);
	top: -1px;
	position: absolute;
	transition: all 0.1s linear;
	cursor: pointer;
}

.buy-sell-toggle.selected.left {
	background: #90CAFF;
}

.buy-sell-toggle.selected.right {
	background: #90CAFF;
}

.buy-sell-toggle.selected .text {
	color: #101222;
}

.buy-sell-toggle * {
	cursor: pointer;
}

