    font-weight: 700;
} */

.buy-sell-toggle.left {
	left: -1px;
}

.buy-sell-toggle.right {
	right: -1px;
}

.buy-sell-toggle-block {
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: flex-start;
}

.title {
	margin-right: 10px;
}

.title,
.text {
	font-size: 20px;
	line-height: 23px;
	color: #90CAFF;
}

.text.selected {
	font-weight: 700;
}
</style>
