<template>
	<div class="auction" @click="hideDropdowns">

		<button class="back-button" @click="$router.back()">
			<img class="back-button-arrow-ref" src="@/assets/newImages/sidebar/double-arrows.svg" />
			<p class="back-button-text">{{ from === 'providing' ? $localizeFilter('liquidity_provider') :
				$localizeFilter('auctions') }}</p>
		</button>

		<div class="title-block">
			<div class="title">{{ $localizeFilter('auction_title', [currency1, currency2]) }}</div>
			<!-- <button class="info-button" @click="openInfoModal"></button> -->
		</div>
		<div class="subtitle"></div>

		<!-- <BuySellToggle :title="null" :variants="actions" @toggle="toggle" /> -->

		<LargeToggle :title="null" :variants="actions.map((a) => {
			return a + '_2'
		})" @toggle="toggle" />

		<div class="bid-button-container">
			<button class="bid-button" @click="submitApplication()">
				{{ $localizeFilter('submit_application') }}
				<img class="plus" src="@/assets/newImages/plus.svg" alt="">
			</button>

			<div class="counter">
				<span class="label">{{ $localizeFilter('next_round') }}: </span>
				<span class="value">{{ countdown }}</span>
			</div>

			<!-- <button class="test-button" @click="clearData()">
                ОЧИСТИТЬ ДАННЫЕ
            </button>

            <button class="test-button" @click="executeRound()">
                ВЫПОЛНИТЬ РАУНД
            </button> -->
		</div>

		<div class="view-for-radiobuttons" v-if="selectedAction === 'buy'">
			<div class="radiobuttons-block">
				<div class="radiobutton-block">
					<input type="radio" id="radio1" name="radio" value="all" v-model="filter" @change="reloadData">
					<label for="radio1" class="radio-label">{{ $localizeFilter('all_bids') }}</label>
				</div>
				<div class="radiobutton-block">
					<input type="radio" id="radio2" name="radio" value="my" v-model="filter" @change="reloadData">
					<label for="radio2" class="radio-label">{{ $localizeFilter('my_bids') }}</label>
				</div>
			</div>
		</div>

		<!-- <div class="bids-title" v-else>{{ $localizeFilter('my_bids') }}</div> -->
		<div class="wrapper">
			<div class="bids-container">
				<div class="bids-header">
					<div class="bids-header-img"></div>{{ $localizeFilter('all_bets') }} \
				</div>
				<ul class="bids-top">
					<li class="auction-bid-item bids-number" v-if="selectedAction !== 'sell'">№</li>
					<li class="auction-bid-item bids-id">{{ $localizeFilter('bid').toUpperCase() }}\</li>
					<li class="auction-bid-item bids-user-id">{{ $localizeFilter('locale_id-ID').toUpperCase() }}\</li>
					<li class="auction-bid-item bids-value">{{ $localizeFilter('share_size').toUpperCase() }}\</li>
					<li class="auction-bid-item bids-price" v-if="selectedAction !== 'sell'">{{
						$localizeFilter('price2').toUpperCase() }}\</li>
					<li class="auction-bid-item bids-progress">{{ $localizeFilter('progress').toUpperCase() }}\</li>
				</ul>
				<div v-for="(bid, idx) in bids" :key="idx">
					<ProvidingAuctionBid :bid="bid" @changeBid="changeBid" @cancelBid="cancelBid" />
				</div>
				<div class="load-more-button-container" v-if="bids.length > 0 && filter === 'all'">
					<button class="load-more-button" @click="loadMoreBids()">
						{{ $localizeFilter('load_more') }}
					</button>
				</div>
			</div>
		</div>


		<AuctionBuyBidModal v-if="showBuyBidModal" :poolId="poolId" :bid="buyBidModalData" :testBalance="testBalance"
			@reloadData="reloadData" @closeModal="closeModals" />
		<AuctionSellBidModal v-if="showSellBidModal" :poolId="poolId" :testBalance="testBalance" @reloadData="reloadData"
			@closeModal="closeModals" />
		<AuctionInfoModal v-if="showInfoModal" @closeModal="closeModals" />
	</div>
</template>
<script>
import { defineComponent } from '@vue/composition-api'
import localizeFilter from '@/filters/localize.filter'
import ProvidingAuctionBid from '../components/ProvidingAuctionBid.vue'
import BuySellToggle from '../components/BuySellToggle.vue'
import LargeToggle from '../components/LargeToggle.vue'
import AuctionBuyBidModal from '../components/newModals/AuctionBuyBidModal.vue'
import AuctionSellBidModal from '../components/newModals/AuctionSellBidModal.vue'
import AuctionInfoModal from '../components/newModals/AuctionInfoModal.vue'

export default defineComponent({
	components: {
		ProvidingAuctionBid,
		BuySellToggle,
		LargeToggle,
		AuctionBuyBidModal,
		AuctionSellBidModal,
		AuctionInfoModal
	},
	props: {
		poolId: {
			type: Number,
			required: true
		},
		currency1: {
			type: String,
			required: true
		},
		currency2: {
			type: String,
			required: true
		},
		from: {
			type: String,
			required: true
		},
	},
	setup() {
	},
	watch: {
		selectedAction(newVal, oldVal) {
			this.$nextTick(() => {
				if (newVal !== oldVal) {
					// this.filter = 'all'
					this.reloadData()
					this.restartInterval()
				}
			})
		},
	},
	data: () => ({
		testBalance: null,
		showBuyBidModal: false,
		buyBidModalData: null,
		showSellBidModal: false,
		showInfoModal: false,
		bids: [],
		pageSize: 20,
		page: 1,
		interval: null,
		filter: 'all' // my
	}),
	computed: {
		// bids() {
		//     return this.$store.getters.auctionProvidingBids
		// },
		actions() {
			return this.$store.getters.liquidityProvidingAuctionActions
		},
		selectedAction() {
			return this.$store.getters.selectedLiquidityProvidingAuctionAction
		},
		skip() {
			return (this.page - 1) * this.pageSize
		},
		limit() {
			return this.pageSize
		},
		countdown() {
			let millisLeft = Math.max(this.$store.getters.liquidityProvidingAuctionNextRoundTimeLeft, 0);
			let secondsLeft = Math.floor(millisLeft / 1000);
			let minutes = Math.floor(secondsLeft / 60);
			let seconds = secondsLeft - minutes * 60;
			if (seconds < 10) {
				seconds = '0' + seconds;
			}
			return `${minutes}:${seconds}`;
			//return '24:12'
			//return this.$store.getters.auctionCountDown
		},
	},
	methods: {
		async clearData() {
			try {
				await this.$store.dispatch('liquidityProvidingAuctionCleanData')
				this.reloadData()
			} catch (e) {
				console.log(e)
			}
		},
		async executeRound() {
			try {
				await this.$store.dispatch('liquidityProvidingAuctionExecuteTradingRound')
				this.reloadData()
			} catch (e) {
				console.log(e)
			}
		},
		restartInterval() {
			if (this.interval) {
				clearInterval(this.interval)
				this.interval = null
				this.interval = setInterval(async () => {
					await this.loadData(true, false)
				}, 3000)
			} else {
				this.interval = setInterval(async () => {
					await this.loadData(true, false)
				}, 3000)
			}
		},
		closeModals() {
			this.showBuyBidModal = false
			this.buyBidModalData = null
			this.showSellBidModal = false
			this.testBalance = null
			this.showInfoModal = false
		},
		toggle(index) {
			this.$store.commit('SET_SELECTED_PROVIDING_AUCTION_ACTION', this.actions[index])
			this.reloadData()
		},
		async reloadData() {
			await this.loadData(true)
		},
		async loadData(silentCatch = false, lazyLoading = false) {
			try {
				if (!lazyLoading) {
					this.page = 1
				}
				else {
					if (this.bids.length % this.pageSize === 0) {
						this.page++
					}
				}
				const data = {
					poolId: this.poolId,
					type: this.selectedAction,
					skip: this.skip,
					limit: this.limit,
					filter: this.filter
				}
				const result = await this.$store.dispatch('getAuctionProvidingBids', data)
				let newBids = result.bids
				this.$store.dispatch('stopNextProvidingAuctionRoundTimer')
				this.$store.dispatch('runNextProvidingAuctionRoundTimer', { timestamp: result.nextRoundTimestamp })
				for (let i = 0; i < newBids.length; i++) {
					newBids[i].type = this.selectedAction
				}
				if (lazyLoading) {
					if (this.bids.length % this.pageSize === 0) {
						this.bids = this.bids.concat(newBids)
						if (newBids.length === 0) {
							this.page--
						}
					} else {
						this.bids = this.bids.slice(0, this.bids.length - (this.bids.length % this.pageSize)).concat(newBids)
					}
				}
				else {
					this.bids = newBids
				}
			}
			catch (e) {
				if (!silentCatch) {
					this.$error(e)
				}
			}
		},
		async submitApplication(bid = null) {
			try {
				const result = await this.$store.dispatch('getLiquidityProvidingBalance')
				this.testBalance = result.balance
				if (this.selectedAction === 'buy') {
					this.openBuyBidModal(bid)
				}
				else {
					this.openSellBidModal()
				}
			}
			catch (e) {
				this.$error(e)
			}
		},
		openBuyBidModal(data = null) {
			if (data) {
				this.buyBidModalData = data
			}
			this.showBuyBidModal = true
		},
		openSellBidModal() {
			this.showSellBidModal = true
		},
		openInfoModal() {
			this.showInfoModal = true
		},
		changeBid(bid) {
			this.submitApplication(bid)
		},
		cancelBid(bid) {
			const data = { requestId: bid.requestId }
			try {
				this.$store.dispatch('cancelLiquidityProvidingAuctionBuyRequest', data)
				this.$message(localizeFilter('bid_cancelled'))
				this.reloadData()
			}
			catch (e) {
				this.$error(e)
			}
		},
		loadMoreBids() {
			clearInterval(this.interval)
			this.interval = null
			this.loadData(true, true)
		}
	},
	async mounted() {
		await this.loadData()

		// window.addEventListener('scroll', () => {
		//     // console.log(window.scrollY + window.innerHeight, document.body.scrollHeight)
		//     if (window.scrollY + window.innerHeight >= document.body.scrollHeight - 100) {
		//         if (this.bids.length % this.pageSize !== 0) {
		//             return
		//         }
		//         // console.log('load more')
		//         this.loadData(true, true)
		//     }
		// })

		this.interval = setInterval(async () => {
			await this.loadData(true, false)
		}, 3000)
	},
	unmounted() {
		this.$store.commit('SET_SELECTED_PROVIDING_AUCTION_ACTION', 'buy')
		clearInterval(this.interval)
		this.interval = null
		this.$store.dispatch('stopNextProvidingAuctionRoundTimer')
	}
})
</script>

